.light-theme {
  color: var(--light-invers);
  background-color: #d8dbe0;
}

.light-theme .modal {
  background-color: #d8dbe;
}

.dark-theme {
  color: #fff;
  background-color: #28292c;
}

.dark-theme .library-text, .dark-theme .movie-card__name, .dark-theme .tui-pagination.goit-pagination .tui-page-btn, .dark-theme .tui-ico-ellip {
  color: #fff;
}

.dark-theme .modal {
  background-color: #28292c;
}

.grey-background-theme, .grey-background-theme .home-footer {
  background-color: #28292c;
  box-shadow: 0 0 15px #444;
}

.grey-background-theme .home-footer__text, .grey-background-theme .home-footer-reference {
  color: var(--secondaryColor);
}

.grey-background-theme .home-footer__icon {
  color: var(--mainAccentColor);
}

.grey-background-theme .body, .grey-background-theme .modal {
  background-color: #28292c;
}

.grey-background-theme .modal__title {
  color: var(--mainAccentColor);
}

.grey-background-theme .modal__info-entry, .grey-background-theme .modal__info-value, .grey-background-theme .modal__about, .grey-background-theme .modal__about-text, .grey-background-theme .movie-card__name {
  color: var(--secondaryColor);
}

.grey-background-theme button {
  background-color: var(--lightThemebackgroundColor);
  border: var(--lightThemebackgroundColor);
}

.grey-background-theme .modal__button-close {
  fill: #e5e5e5;
  background-color: #28292c;
}

.home-footer.grey-background-theme {
  background-color: #28292c;
  box-shadow: 0 0 15px #444;
}

.toggle-switch {
  --dark-invers: #e5e5e5;
  --light-invers: #121213f7;
}

.switch-label {
  background-color: var(--light-invers);
  border: 1px solid #920e0e;
}

.checkbox:checked ~ .slider {
  background-color: var(--secondaryColor);
}

.slider:before {
  -webkit-box-shadow: inset 12px -4px 0px 0px var(--dark-invers);
  box-shadow: inset 12px -4px 0px 0px var(--dark-invers);
  background-color: var(--light-invers);
}

.checkbox:checked ~ .slider:before {
  background-color: var(--light-invers);
}

@media screen and (prefers-color-scheme: dark) {
  body {
    color: var(--secondaryColor);
    background: #28292c;
  }
}

